* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: rgba(250, 250, 250, .59);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  cursor: pointer;
  color: #fff;
  text-shadow: 0 1px #528ecc;
  background: #79bbff linear-gradient(#79bbff 5%, #378de5 100%);
  border: 1px solid #337bc4;
  border-radius: 5px;
  padding: 12px 44px;
  font-family: Arial;
  font-size: 17px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  box-shadow: 3px 4px #1564ad;
}

button[data-start], button[data-stop] {
  margin-right: 40px;
}

